import React, { useState } from "react";
import styled from "styled-components";
import styles from "./Buttons.module.css";
import { WhatsappShareButton, WhatsappIcon } from "react-share";

const Buttonn = styled.button`
  font-family: "Century Gothic", serif;
  padding: 10px 50px;
  color: ${(props) => (props.mouse ? "red" : "blue")};
  font-size: 50px;
`;

function Button() {
  const [mouse, setMouse] = useState(false);

  const mouseDown = () => setMouse(!mouse);

  // return <Buttonn className={styles.SampleButton}>Start</Buttonn>;
  return (
    <WhatsappShareButton
      title="Sharing something"
      url="https://hhcourses-testsite.herokuapp.com/#/topic/2.2#2.2.1"
    >
      <WhatsappIcon></WhatsappIcon>
    </WhatsappShareButton>
  );
}

export default Button;
